<template>
    <div>
        <van-cell v-for="(item,index) in faq" :key="index" :title="item.q" is-link @click="linkTo(index)"></van-cell>
    </div>
</template>

<script>
  export default {
    name: "FAQ",
    data() {
      return {
        faq: [
          {
            q: '你好看吗？',
            a: '很明显好看'
          }, {
            q: '你喜欢的歌手是谁啊？',
            a: 'Jay chou'
          }, {
            q: '还有什么问题？',
            a: '巴拉巴拉巴拉巴拉巴拉巴拉巴拉巴拉巴拉巴拉巴拉巴拉巴拉巴拉巴拉巴拉巴拉'
          }, {
            q: '这是常见问题四',
            a: '这是常见问题四的答案'
          },
        ]
      }
    },
    methods: {
      linkTo: function (i) {
        this.$router.push({
          path: '/FAQ/answer',
          query: {
            detail: this.faq[i]
          }
        });
      }
    }
  }
</script>

